import React, { Component } from "react";
import Card from "react-md/lib/Cards/Card";
import CardTitle from "react-md/lib/Cards/CardTitle";
import Button from "react-md/lib/Buttons";
import CardText from "react-md/lib/Cards/CardText";
import Link from "gatsby-link";
import moment from "moment";
import Media, { MediaOverlay } from "react-md/lib/Media";
import PostTags from "../PostTags/PostTags";
import PostCover from "../PostCover/PostCover";
import config from "../../../data/SiteConfig";
import "./PostPreview.scss";

class PostPreview extends Component {
  constructor(props) {
    super(props);
    this.state = {
      mobile: true
    };
    this.handleResize = this.handleResize.bind(this);
    this.weekdayKo = this.weekdayKo.bind(this);
  }
  componentDidMount() {
    this.handleResize();
    window.addEventListener("resize", this.handleResize);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.handleResize);
  }

  handleResize() {
    if (window.innerWidth >= 640) {
      this.setState({ mobile: false });
    } else {
      this.setState({ mobile: true });
    }
  }
  weekdayKo(wkDay) {
    let wKo = "";
    switch (wkDay) {
      case "Monday":
        wKo = "월요일";
        break;
      case "Tuesday":
        wKo = "화요일";
        break;
      case "Wednesday":
        wKo = "수요일";
        break;
      case "Thursday":
        wKo = "목요일";
        break;
      case "Friday":
        wKo = "금요일";
        break;
      case "Saturday":
        wKo = "토요일";
        break;
      case "Sunday":
        wKo = "일요일";
        break;
      default:
        wKo = "?";
    }
    return wKo;
  };
  render() {
    const { postInfo, fileEdges } = this.props;
    const { mobile } = this.state;
    const expand = mobile;
    /* eslint no-undef: "off" */
    // const coverHeight = mobile ? 162 : 225;
    const coverHeight = mobile ? 162 : 162;
    return (
      <Card key={postInfo.path} raise className="md-grid md-cell md-cell--12">
        <Link style={{ textDecoration: "none" }} to={postInfo.path}>
          <Media style={{ height: coverHeight, paddingBottom: "0px" }}>
            <PostCover
              fileEdges={fileEdges}
              postNode={postInfo}
              coverHeight={coverHeight}
            />
          </Media>
        </Link>
        {/* <PostTags tags={postInfo.tags} /> */}
        <Link style={{ textDecoration: "none" }} to={postInfo.path}>
          <CardTitle
            expander={expand}
            title={postInfo.title}
            subtitle={`${moment(postInfo.startAt).format(config.dateFormatForKo)} ${this.weekdayKo(moment(postInfo.startAt).format(config.dateFormatWeekday))}부터 @${postInfo.place}`}
          />
        </Link>
        {/* <CardTitle
          expander={expand}
          title={postInfo.title}
          subtitle={`Published on ${moment(postInfo.date).format(
            config.dateFormat
          )}`}
        /> */}

        <CardText expandable={expand}>
          <p>{postInfo.excerpt}</p>
        </CardText>
      </Card>
    );
  }
}

export default PostPreview;



