import React from "react";
import { Grid, Cell } from 'react-md';
import PostPreview from "../PostPreview/PostPreview";
import "./PostListing.scss";

class PostListing extends React.Component {
  getPostList() {
    const postList = [];
    this.props.postEdges.forEach(postEdge => {
      postList.push({
        path: postEdge.node.fields.slug,
        tags: postEdge.node.frontmatter.tags,
        cover: postEdge.node.frontmatter.cover,
        title: postEdge.node.frontmatter.title,
        excerpt: postEdge.node.frontmatter.shortDescription,
        startAt: postEdge.node.frontmatter.startAt,
        place: postEdge.node.frontmatter.place,
        date: postEdge.node.frontmatter.date
      });
    });
    return postList;
  }
  render() {
    const postList = this.getPostList();
    return (
      <Grid className="grid-post-list md-grid md-grid--no-spacing md-cell--middle">
        {postList.map(post => (
          <Cell
            className="mobile-fix"
            key={post.title}
          >
            <PostPreview
              postInfo={post}
              fileEdges={this.props.fileEdges}
            />
          </Cell>
        ))}
        {/* <div className="md-grid md-grid--no-spacing md-cell--middle">
          <div className="md-grid md-cell--3 md-cell--left mobile-fix">
            {postList.map(post => (
              <PostPreview
                key={post.title}
                postInfo={post}
                fileEdges={this.props.fileEdges}
              />
            ))}
          </div>
        </div> */}
      </Grid>
    );
  }
}

export default PostListing;



